import React from "react"
import Link from "gatsby-link"
import * as PropTypes from "prop-types"
import ReactMarkdown from "react-markdown"

import { rhythm } from "../utils/typography"

const propTypes = {
  data: PropTypes.object.isRequired,
}

class WorkTemplate extends React.Component {
  render() {
    const work = this.props.data.contentfulWork

    return (
      <div>
        <h1>{work.title}</h1>
        <section>
          <p><img src={work.media[0].file.url} alt={work.media.title} /></p>
          <p>{work.longDescription.longDescription}</p>
        </section>
      </div>
    )
  }
}

WorkTemplate.propTypes = propTypes

export default WorkTemplate

export const pageQuery = graphql`
  query workItemQuery($id: String!) {
    contentfulWork(id: { eq: $id }) {
      title
      id
      media {
        title
        file {
          url
        }
        description
      }
      shortDescription{
        shortDescription
      }
      longDescription{
        longDescription
      }

    }
  }
`



